body{
    margin: 0;
    padding: 0;
}
.breakfast{
    background-color: black;
}
.header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    filter: brightness(100%);
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 3;
}
.menuIcon:hover{
    cursor: pointer;
}
.menuIcon{
    position: relative;
    left: 15px;
    color: white;
}
.menuIconBar{
    height: 4px;
    width: 27px;
    margin: 4px;
    border-radius: 6px;
    background-color: white;
}
.closeMenuIcon:hover{
    cursor: pointer;
}
.closeMenuIcon{
    filter:brightness(100%);
    color: white;
    font-size: 1.63rem;
    font-weight: 500;
}
.sidebarMenu{
    filter: brightness(100%);
    height: 63vh;
    width: 15vw;
    color: white;
    background-color: #343434;
    text-align: center;
    position: fixed;
    border-radius: 20px;
    z-index: 2;
}
.sidebarMenuItems span:hover{
    cursor: pointer;
}
.sidebarMenuItems{
    padding: 25px 0;
    font-size: 0.94rem;
    font-weight: 500;
}
hr{
    margin-top: 30px;
    margin-bottom: 30px;
    width: 120px;
    height: 1px;
    border-radius: 2px;
    background-color: white;
}
.nav{
    font-size: 1rem;
    justify-items: flex-end;
    color: white;
    position: relative;
    right: 20px;
    font-weight: 500;
}
.navItem:hover{
    cursor: pointer;
}
.navItem{
    display: inline-block;
    padding: 10px 30px;
}
.section{
    filter: brightness(100%);
    color: white;
    z-index: 1;
}
.topHeading{
    display: flex;
    position: relative;
    top:42px;
    justify-content: center;
    padding: 10px 0;
    font-size: 2.5rem;
    letter-spacing: 8px;
    text-shadow: 2px 1px 9px white;
}
.trackCartAlert{
    position: absolute;
    right: 0;
    filter: brightness(0%);
    animation: disappear 6s linear 3s infinite alternate;
}
@keyframes disappear{
    to{filter: brightness(100%);}
}
.arrowSymbol{
    font-size: 3.75rem;
    display: inline-block;
    position: relative;
    right: 17px;
    bottom: 25px;
    transform: rotate(220deg);
}
.trackCartAlertText{
    display: inline-block;
    position: relative;
    top: 25px;
    right: 80px;
    font-size: 0.94rem;
    border-radius: 12px;
    text-align: center;
    width: 140px;
    padding: 2.5% 0;
    font-weight: 500;
    background-color: rgb(214, 11, 11);
}
.breakfastList{    
    font-size: 2.19rem;
}
.breakfastMenuItemImg{
    height: 50vh;
    width: 25vw;
    border-radius: 25px;
}
.breakfastMenuItem{
    display: flex;
    justify-content: space-between;
}
.breakfastMenuItemDish{
    font-size: 1.44rem;
}
.breakfastMenuItemPrice{
    font-size: 1.25rem;
}
.addButton{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    width: 88px;
    color: black;
    font-size: 1.13rem;
    font-weight: 500;
    border-radius: 15px;
    background-color: #ADD8E6;
    position: relative;
    top: 15px;
}
.addButton:hover{
    cursor: default;
}
.breakfastItemNumsAlert{
    font-size: 1rem;
    position: relative;
    top: 25px;
    visibility: visible;
}
.breakfastItemCount{
    position: relative;
    visibility: visible;
}
.breakfastItemCount div{
    height: 27px;
    width: 27px;
    font-size: 1.56rem;
    display: inline-block;
    color: black;
    position: relative;
    top: 20px;
}
.breakfastItemCount span{
    position: relative;
    bottom: 5px;
}
.arithChar{
    background-color: #FFFFF0;
}
.arithChar:hover{
    cursor: default;
}
.itemCount{
    background-color: rgba(250,249,246,0.88);
}
.itemCount:hover{
    cursor: default;
}
.breakfastRow1{
    display: inline-block;
    position: relative;
    top: 90px;
}
.breakfastRow2{
    display: inline-block;
    position: relative;
    top: 150px;
}
.breakfastRow3{
    display: inline-block;
    position: relative;
    top: 210px;
}
