.footer{
    background-color: black;
    filter: brightness(100%);
    color: white;
    padding: 60px 0 0 0;
    position: relative;
    top:240px;
}
.footContent1{
    display: flex;
    justify-content: center;
    margin-top: 5px;
    padding: 10px 0;
}
.footContent2{
    display: flex;
    justify-content: center;
    position: relative;
    bottom: 2px;
}
.footContent3{
    display: flex;
    justify-content: center;
    margin-top: 25px;
}
.footImages{
    filter: brightness(100%);
    display: flex;
    position: relative;
    bottom: 15px;
    margin-top: 20px;
    justify-content: center;
}
.footLogo{
    padding: 0 4px;
    height: 24px;
    width: 24px;
    border-radius: 50%;
}
.privacyAndTerms{
    filter: brightness(100%);
    color: white;
    display: flex;
    justify-content: center;
    position: relative;
    bottom: 10px;
    margin-top: 17px;
    font-size: 0.81rem;
}

@media screen and (max-width: 1024px) {
    .nav{
        right: 0;
        margin-top: 1.5%;
        align-self: flex-end;
        width: fit-content;
    }
    .navItem{
        font-size: 1rem;
        color: white;
        padding: 1% 7.5%;
        display: inline-block;
        width:fit-content;
    }
    #homepageLink, #cartLink, #loginLink{
        font-size: 1rem;
        padding: 1% 7.5%;
        display: inline-block;
        width:fit-content;
    }
    #homepageLink{
        margin-left: -2%;
    }
    #cartLink{
        margin-top: 7%;
        margin-left: 25.6%;
        padding-right: 7.6%;
        position: relative;
        left: 3.7%;
    }
    #loginLink{
        width: 50px;
        position: relative;
        left: 2%;
    }
    .sidebarMenu{
        width: 50vw;
        height:fit-content;
    }
    .topHeading{
        position: relative;
        top: 95px;
        left: 2%;
    }
    .trackCartAlert{
        right:-15%;
    }
    .arrowSymbol{
        margin-top: 9.7%;
        position: relative;
        left: -19%;
        font-size: 2.5rem;
    }
    .trackCartAlertText{
        position: relative;
        top: 7px;
        left: -40%;
    }
    .breakfastMenuItemImg{
        height: 30vh;
        width: 70vw;
    }
    .addButton{
        height: 36px;
    }
    .addButton span{
        margin-top: 1.6%;
    }
    .breakfastItemCount span{
        bottom: 1px;
    }
    .breakfastRow1{
        top: 0;
    }
    .breakfastRow2{
        top: 0;
    }
    .breakfastRow3{
        top: 0;
    }
    .breakfastMenuItem1{
        margin-top: 39%;
    }
    .breakfastMenuItem2{
        display: inline-block;
        padding: 15% 0;
    }
    .breakfastMenuItem4{
        display: inline-block;
        padding: 15% 0;
    }
    .breakfastMenuItem6{
        display: inline-block;
        padding: 15% 0;
    }
    .breakfastMenuItem8{
        display: inline-block;
        padding-top: 15%;
    }
    .footer{
        position: relative;
        top: -4px;
        margin-top: 6%;
    }
}
    